.libre-baskerville-regular {
  font-family: "Libre Baskerville", serif;
  font-weight: 400;
  font-style: normal;
}

.libre-baskerville-bold {
  font-family: "Libre Baskerville", serif;
  font-weight: 700;
  font-style: normal;
}

.libre-baskerville-regular-italic {
  font-family: "Libre Baskerville", serif;
  font-weight: 400;
  font-style: italic;
}

.barriecito-regular {
  font-family: "Barriecito", system-ui;
  font-weight: 400;
  font-style: normal;
}
      
body {
    background: black;
    color: azure;
    @extend .libre-baskerville-regular;
}


.header { 
    
    background: red;
    color: azure;    
    background-image:url(../img/kateandnickwebcrunched.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    
    h2 {
        font-family: "Barriecito", system-ui;
    }
}

.card {
    margin-left: auto;
    margin-right: auto;
}

.about {
     background: black;
    color: azure;  
    background-image:url(../img/honeyrose1.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
       
}